html {
  scroll-behavior: smooth;
}

body {
  background-color: #1c2023;
  font-family: Rubik, sans-serif;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 767px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (width >= 768px) {
  .container {
    max-width: 768px;
  }
}

@media (width >= 1200px) {
  .container {
    max-width: 1200px;
  }
}

button {
  cursor: pointer;
  border: none;
  display: block;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

input {
  border: none;
  outline: none;
}

* {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.header {
  background-color: #0000;
  padding-top: 36px;
}

.header__pages {
  align-items: center;
  display: flex;
}

.header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.is-hidden {
  opacity: 0;
}

.nav-list {
  padding-left: 50px;
  display: none;
}

@media (width >= 1200px) {
  .nav-list {
    opacity: 1;
    gap: 31px;
    display: flex;
  }
}

.nav-list__item {
  align-items: center;
  display: flex;
}

.nav-list__btn {
  margin-left: 9px;
}

.nav-list__link {
  letter-spacing: 0%;
  text-align: left;
  color: #ffffff80;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  line-height: 193%;
}

.nav-list__link:hover {
  color: #8856d9;
}

.language {
  color: #fff;
  cursor: pointer;
  font-weight: 700;
}

summary {
  list-style: none;
}

.header-btn__icon, .nav-list__icon {
  fill: #fff;
}

.nav-list__icon {
  transition: transform .5s;
}

.language-switcher[open] .nav-list__icon {
  transform: rotate(180deg);
}

.header-btn, .nav-list__btn {
  background-color: #0000;
}

.header-time {
  align-items: center;
  gap: 10px;
  display: flex;
}

.header-time__title {
  letter-spacing: 0%;
  text-align: left;
  color: #8c96a0;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
}

.header-time-list {
  gap: 10px;
  padding-left: 19px;
  display: flex;
}

.header-time-list__item {
  text-align: center;
}

.header-time-list__num {
  letter-spacing: 0%;
  text-align: left;
  color: #a9c7c7;
  font-size: 40px;
  font-weight: 700;
  line-height: 100%;
}

.header-time-list__sym {
  letter-spacing: 0%;
  text-align: left;
  color: #8c96a0;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
}

.header, .hero {
  background-image: url("ragnarokheader.2694b9ed.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
}

.snowfall {
  pointer-events: none;
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.snowfall__video {
  object-fit: cover;
  opacity: .1;
  width: 100%;
  height: 100%;
}

.hero {
  z-index: 2;
  padding: 83px 0 187px;
}

.hero__title {
  letter-spacing: 6%;
  text-align: left;
  color: #fff;
  max-width: 538px;
  font-size: 100px;
  font-weight: 600;
  line-height: 100px;
}

.hero__text {
  letter-spacing: 0%;
  text-align: left;
  color: #a9c7c7;
  max-width: 543px;
  padding: 27px 0 50px;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.hero__btn {
  letter-spacing: 0%;
  text-align: left;
  color: #fff;
  text-transform: uppercase;
  background-color: #8856d9;
  border-radius: 6px;
  padding: 12px 36px;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
}

.navigation .nav-list.is-hidden {
  display: flex;
}

.navigation .nav-list {
  display: none;
}

.editions__title {
  letter-spacing: 0%;
  text-align: center;
  color: #fff;
  padding: 54px 0 23px;
  font-size: 40px;
  font-weight: 600;
  line-height: 47px;
}

.editions-list {
  flex-flow: wrap;
  justify-content: flex-end;
  align-items: flex-end;
  gap: 16px;
  display: flex;
}

.editions-list__item {
  background: linear-gradient(#141615, #1c2023 100%);
  border-radius: 6px;
  padding: 17px;
}

.editions-list__img {
  width: 316px;
  height: 172px;
}

.editions-list__title {
  letter-spacing: 0%;
  text-align: center;
  color: #fff;
  padding: 21px 0 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
}

.editions-list__text {
  letter-spacing: 0%;
  text-align: center;
  color: #a9c7c7;
  padding-bottom: 37px;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
}

.editions-list__option {
  letter-spacing: 0%;
  text-align: left;
  color: #8c96a0;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

.editions-list__option:nth-child(2) {
  padding: 12px 0;
}

.editions-list__cost {
  flex-flow: column wrap;
  justify-content: flex-end;
  align-items: flex-end;
  display: flex;
}

.editions-list__price {
  letter-spacing: 0%;
  text-align: center;
  color: #fff;
  padding: 6px 0 14px;
  font-size: 19px;
  font-weight: 700;
  line-height: 23px;
}

.editions-list__btn {
  letter-spacing: 0%;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #8856d9;
  border-radius: 6px;
  padding: 8px 19px;
  font-size: 13px;
  font-weight: 600;
  line-height: 15px;
}
